import find from './find';
import isObject from './isObject';
import isFunction from './isFunction';

export default function findIndex(subject, predicate) {
    for(const i in subject) {
        if(isFunction(predicate) && predicate(subject[i], i)) {
            return i;
        }
        else if(isObject(predicate) && find(predicate, subject[i])) {
            return i;
        }
        else if(predicate === subject[i]) {
            return i;
        }
    }

    return -1;
}
